.Second_content {
    /* border:1px solid rgb(255, 0, 0); */
    width: 100%;
    flex-grow: 1;
    display: none;
    /* display: flex; */
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

@media (max-width:880px) {
    .Second_content {
        height: 90%;
    }
}

.second_title_in_main , .second_btn_in_main {
    /* border: 1px solid black; */
    width: 100%;
    height: 90px;
}

.second_title_in_main {
    text-align: center;
    line-height: 90px;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: bolder;
    color: #bba78c;
    font-size: 6rem;
    text-shadow: -3px 2px 20px black;
}

.second_text_in_main {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: justify;
    text-align-last: center;
    font-family: Arial, Helvetica, sans-serif;
    width: 100%;
    /* height: 50px; */
    margin: 10px;
    padding-left: 15px;
    padding-right: 15px;

    /* border: 1px solid black; */
}
.second_text {
    /* border: 1px solid black; */
    max-width: 600px;
    height: 100%;
    color: #593200;
    font-weight: bold;
    
}

.second_btn_in_main {
    display: flex;
    justify-content: center;
    align-items: center;
}

.second_btn_in_main > button {
    background-color: #593200;
    border-radius: 7px;
    color: #bba78c;
    border: none;
    width: 200px;
    height: 47px;
    font-size: 20px;
    box-shadow:-2px 2px 20px 2px #00000059;
}

@media (max-width:880px) {
    .Second_content {
        display: flex;
    }
}


@media (max-width:430px) {
    .second_title_in_main{
        font-size: 4rem;
    }
}